
.date{
	width: 100%;
	background-color: red;
	padding-left:5px;
	height: 30px;
	line-height: 30px;
}
.item{
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-align:center;
	    -ms-flex-align:center;
	        align-items:center;
	-webkit-box-sizing:border-box;
	        box-sizing:border-box;
	padding:15px 0;
}
.item .info-img{
	-webkit-box-flex:80px;
	    -ms-flex:80px 0 0px;
	        flex:80px 0 0;
	margin-right: 15px;
}
.item .info-desc{
	height:120px;
	-webkit-box-flex:1;
	    -ms-flex:1;
	        flex:1;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-orient:vertical;
	-webkit-box-direction:normal;
	    -ms-flex-direction:column;
	        flex-direction:column;
	-ms-flex-pack:distribute;
	    justify-content:space-around;
	overflow: hidden;
}
.item .info-desc .title{
	font-size: 16px;
	color: #333;
}
.item .info-desc .director{
	font-size: 12px;
	color: #777777;
}
.item .info-desc .casts{
	font-size: 12px;
	color: #777777;
	white-space: nowrap;
}
.item .info-desc .hasWatched{
	color: #333333;
	font-size: 12px;
}



